<template>
    <div class="page-container" style="width: 50%">
      <ul>
        <li v-for="(talk, index) in talkList" :key="talk.id">
            <div class="header" style=" width: 400px; height: 30px;">
                <strong class="primary-font" :style="{float: 0 == index % 2 ? 'left' : 'right'}">
                    {{userList[index % 2]}}
                </strong>
            </div>
           <div>
                <p>{{talk.message}}</p>
                <small class="text-muted">
                    <span class="glyphicon glyphicon-time"></span>14 mins ago
                </small>
           </div>
        </li>
    </ul>
    <v-textarea class="mx-2" rows="1" v-model="message" prepend-icon="mdi-comment"></v-textarea>
    <v-btn icon @click="sendMessage">
        <v-icon>mdi-send</v-icon>
    </v-btn>
    </div>
</template>
<script>
import { mapState } from 'vuex';

export default {
    name: 'Chat',
    computed: {
		...mapState(['userInfo']),
	},
    created() {
        this.$socket.on('chat', (data) => {
            this.talkList.push({
                id : this.getUUID(),
                message :  `${data.message}\n`,
            });

            console.log(data)
        })
    },
    data() {
        return {
            userList: ['Jack Sparrow', 'Me'],
            talkList: [
                {
                    id: 1,
                    message: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur bibendum ornare dolor, quis ullamcorper ligula sodales.',
                },
                {
                    id: 2,
                    message: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur bibendum ornare dolor, quis ullamcorper ligula sodales.',
                },
                {
                    id: 3,
                    message: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur bibendum ornare dolor, quis ullamcorper ligula sodales.',
                }
            ],
            message: '',
            textarea: '',
        }
    },
    methods: {
        sendMessage() {
            if(0 === this.message.trim().length) return;

            this.$socket.emit('chat', { message: this.message });

            this.message = '';
        },
        getUUID() {
            return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
                (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
            );
        }
    }
}
</script>

<style>
    .md-app {
        height: 800px;
        border: 1px solid rgba(#000, .12);
    }

    .md-textarea {
        height: 300px;
    }
</style>